<template>
    <PageHeader :title="t(`pages.tracks.title`)"
        :detail="!!data.editorTracks?.length"
        id="item-head-buttons"
        v-show="!data.trackEditorMode && !data.licenseTrackEditorMode"
    />

    <div class="box" v-show="!data.trackEditorMode && !data.licenseTrackEditorMode">
        <div class="item-detail">
            <Tracks
                :canAdd="false"
                @select="onSelectTracks"
                @add="addTracks"
                @edit="editTracks"
                @licenses="editTracksLicenses"
                ref="tracks"
            />
        </div>
    </div>

    <!-- Редактирование треков -->
    <TrackEditor v-if="data.trackEditorMode"
        :items="data.editorTracks"
        ref="trackEditor"
        @remove="onRemoveTrack"
        @update="onUpdateTrack"
        @add="onAddTrack"
        @close="closeTrackEditorMode"
    />

    <!-- Редактирование лицензий треков -->
    <LicenseTrackEditor v-if="data.licenseTrackEditorMode"
        :items="data.editorTracks"
        ref="licenseTrackEditor"
        @close="closeLicenseTrackEditorMode"
    />
</template>

<script setup>
    import { reactive, computed, ref } from 'vue';
    import PageHeader from '@/components/blocks/PageHeader/PageHeader.vue';

    import Tracks from '@/components/blocks/Tracks.vue';
    import TrackEditor from '@/components/blocks/TrackEditor/TrackEditor.vue';
    import LicenseTrackEditor from '@/components/blocks/LicenseTrackEditor/LicenseTrackEditor.vue';

    import { useI18n } from 'vue-i18n';

    const { t } = useI18n();
    const tracks = ref(null)
    const trackEditor = ref(null);
    const licenseTrackEditor = ref(null);

    const data = reactive({
        trackEditorMode: false,
        licenseTrackEditorMode: false,
        editorTracks: null
    });

    // Tracks
    const onSelectTracks = (tracks) => {
        data.editorTracks = tracks?.length ? tracks : null;
    }

    const addTracks = (count) => {
        data.trackEditorMode = true;

        // 
        setTimeout(() => {
            trackEditor.value?.create(count || 1);
        });
    }

    const editTracks = (tracks) => {
        data.editorTracks = tracks;
        data.trackEditorMode = true;
    }

    const closeTrackEditorMode = () => {
        data.trackEditorMode = false;
    }
        // Events
        const onAddTrack = (track) => {
            if (track?.id) {
                tracks.value.addItem(track);
            }
        }

        const onRemoveTrack = (id) => {
            if (id) {
                tracks.value.removeItems([{ id }], true)
            }
        }

        const onUpdateTrack = (track) => {
            if (track?.id) {
                tracks.value.updateItem(track);
            }
        }

    const editTracksLicenses = (tracks) => {
        data.editorTracks = tracks;
        data.licenseTrackEditorMode = true;
    }

    const closeLicenseTrackEditorMode = () => {
        data.licenseTrackEditorMode = false;
    }
</script>

<style lang="scss" scoped>
    @include studio.default-box;
</style>
